define([
    'utils/helpers',
    'utils/underscore',
    'view/components/tooltip',
    'utils/ui',
    'templates/setting.html'
], function(utils, _, Tooltip, UI, SettingTemplate) {

    var Setting = Tooltip.extend({
        setup : function (_api , _model) {
            this.api = _api;
            this.model = _model;
            if(!this.iconUI){
                this.iconUI = new UI(this.el, {'useHover': true});

                this.toggleOpenStateListener = this.toggleOpenState.bind(this);
                this.openTooltipListener = this.openTooltip.bind(this);
                this.closeTooltipListener = this.closeTooltip.bind(this);

                this.openTooltipListener = this.onOpenTooltip.bind(this);

                this.selectListener = this.onSelect.bind(this);
            }

            this.reset();
            this.iconUI = new UI(this.el, {'useHover': true})
                .on('tap', this.toggleOpenStateListener)
                .on('click', this.openTooltipListener);
                //.on('over', this.openTooltipListener)
                //.on('out', this.closeTooltipListener);

            var innerHtml = this.menuTemplate(_api);
            var elem = utils.createElement(innerHtml);
            this.addContent(elem);
            this.contentUI = new UI(this.content);
            this.elementContain = elem;

            this.contentUI.on('click tap', this.selectListener);
            utils.toggleClass(this.el, 'jw-hidden', false);
            this.updateHtml();
            // Update the menu if the provider changes
            this.model.on('change:provider', this.updateHtml, this);
            this.model.on('change:captionsList' , this.updateHtml, this);
            this.model.on('itemReady' , this.updateHtml, this);
        },
        onOpenTooltip : function (evt){
            try {
                if (evt.target.className.indexOf('jw-icon-tooltip') > -1){
                    this.toggleOpenState(evt);
                }
            } catch (e){
                this.toggleOpenState(evt);
            }

        },
        updateHtml : function(){
            var targetDiv = this.elementContain.getElementsByClassName('jw-setting-captions')[0];
            if (targetDiv) {
                targetDiv.style.display = 'none';
                var captionData = this.api.getCaptionsList();
                if (typeof(captionData) !== 'undefined' && captionData.length > 1) {
                    targetDiv.style.display = 'block';
                    utils.toggleClass(this.el, 'jw-hidden', false);
                } else {
                    utils.toggleClass(this.el, 'jw-hidden', true);
                }
            } 
        },
        getCookie : function(key) {
            var keyValue = document.cookie.match('(^|;) ?' + key + '=([^;]*)(;|$)');
            return keyValue ? keyValue[2] : null;
        },
        setCookie : function(key, value, date) {
            if (!date) {
                date = 31536000000;
            }
            var expires = new Date();
            expires.setTime(expires.getTime() + date);
            document.cookie = key + '=' + value + '; path=/; expires=' + expires.toUTCString();
        },
        menuTemplate : function(_api) {
            var newList = {
                currentOption: _api.getCaptionStyle(),
                playerID : _api.id,
                colorList: [{
                    color : '#080808'
                } , {
                    color : '#0000FF'
                } , {
                    color : '#01FF00'
                } , {
                    color : '#01FFFF'
                } , {
                    color : '#FF0000'
                } , {
                    color : '#FF00FF'
                } , {
                    color : '#FFFF00'
                } , {
                    color : '#FFFFFF'
                }]
            };
            for (var i = 0; i < newList.colorList.length; i++){
                if (newList.colorList[i].color === newList.currentOption.color){
                    newList.colorList[i].activeColor = true;
                }
                if (newList.colorList[i].color === newList.currentOption.textShadow){
                    newList.colorList[i].activeLine= true;
                }
            }
            return SettingTemplate(newList);
        },

        onSelect: function(evt) {
            var elem = evt.target;
            var arrElem = [];
            var i = 0;
            if(elem.tagName === 'A') {
                var classes = utils.classList(elem);
                // find the class with a name of the form 'jw-item-1'
                var item = _.find(classes, function (c) { return c.indexOf('button-player-caption-delay') === 0;});
                if (item) {
                    arrElem = elem.parentElement.parentElement.getElementsByClassName('caption-delay');
                    if ((item.split('-')[4]) === 'up'){
                        arrElem[0].value = Number(arrElem[0].value) + 0.5;
                    }  else {
                        arrElem[0].value = Number(arrElem[0].value) - 0.5;
                    }
                    this.trigger('captiondelay', Number(arrElem[0].value));

                }

                item = _.find(classes, function (c) { return c.indexOf('button-player-caption-back') === 0;});
                if (item) {
                    var value = true;
                    if (item.split('-')[4] === 'hide'){
                        value = false;
                    }
                    this.trigger('captionback', value);
                    arrElem = elem.parentElement.parentElement.getElementsByClassName('bb-remove');
                    for (i = 0; i < arrElem.length; i++){
                        arrElem[i].className = arrElem[i].className.replace(/\bactive\b/,'').trim();
                    }
                    elem.className += ' active';
                }

                item = _.find(classes, function (c) { return c.indexOf('button-caption-color') === 0;});
                if (item) {
                    this.trigger('captioncolor', elem.getAttribute('data-color'));
                    arrElem = elem.parentElement.parentElement.getElementsByClassName('bclc-remove');
                    for (i = 0; i < arrElem.length; i++){
                        arrElem[i].className = arrElem[i].className.replace(/\bactive\b/,'').trim();
                    }
                    elem.className += ' active';
                }

                item = _.find(classes, function (c) { return c.indexOf('button-caption-line') === 0;});
                if (item) {
                    this.trigger('captionline', elem.getAttribute('data-color'));
                    arrElem = elem.parentElement.parentElement.getElementsByClassName('bcll-remove');
                    for (i = 0; i < arrElem.length; i++){
                        arrElem[i].className = arrElem[i].className.replace(/\bactive\b/,'').trim();
                    }
                    elem.className += ' active';
                }

                item = _.find(classes, function (c) { return c.indexOf('button-player-caption-size') === 0;});
                if (item) {
                    arrElem = elem.parentElement.parentElement.getElementsByClassName('caption-size');
                    if ((item.split('-')[4]) === 'up'){
                        arrElem[0].value = Number(arrElem[0].value) + 2;
                    }  else {
                        arrElem[0].value = Number(arrElem[0].value) - 2;
                    }
                    this.trigger('captionsize', Number(arrElem[0].value));
                }
            }
        },

        selectItem : function(item) {
            this.setup(this.originalList, item);
        },

        reset : function() {
            this.iconUI.off();
            if(this.contentUI){
                this.contentUI.off().destroy();
            }
            this.removeContent();
        }
    });

    return Setting;
});



